<template>
    <page-layout :title="title">
        <a-card title="上传文件" style="padding:0" :bordered="false">
            <a-upload-dragger name="file" :multiple="false" :fileList="fileList" :beforeUpload="beforeUpload" :remove="handleRemove">
                <p class="ant-upload-drag-icon">
                <a-icon type="inbox" />
                </p>
                <p class="ant-upload-text">单击或拖动文件到此区域</p>
                <p class="ant-upload-hint">上传业绩清单后可进行业绩分析</p>
            </a-upload-dragger>
            <a-button
            type="primary"
            @click="handleUpload"
            :disabled="fileList.length === 0"
            :loading="uploading"
            style="margin-top: 16px"
            block
            >
            {{uploading ? '正在上传并分析' : '开始上传' }}
            </a-button>
        </a-card>
        <a-card title="分析结果" style="padding:0" :bordered="false">
            <a-table
                :dataSource="dataSource"
                :columns="columns"
                @change="handleChange"
                :scroll="{ x: 1300 }"
                :pagination="pagination"
                :loading="uploading"
            >
            </a-table>
        </a-card>
    </page-layout>
</template>

<script>
import PageLayout from "@/components/Page/PageLayout";
import ACard from "ant-design-vue/lib/card"
import AIcon from 'ant-design-vue/lib/icon'
import AUploadDragger from "ant-design-vue/lib/upload/Dragger"
import addTrackRecord from '@/api/trackRecord'
import AButton from 'ant-design-vue/lib/button'
import ATable from "ant-design-vue/lib/table";
import { getPerformanceResult } from '@/api/performanceStatistics' 

export default {
    components:{
        PageLayout,
        AButton,
        ACard,
        AIcon,
        AUploadDragger,
        ATable
    },
    data(){
        return{
            fileList:[],
            uploading: false,
            dataSource:[],
            loading: false,
            filteredInfo: null,
            sortedInfo: null,
            pagination: {
                showQuickJumper: true,
                pageSize: 10
            }
        }
    },
    computed:{
        title(){
            return this.$route.meta.title
        },
        columns() {
            let { sortedInfo, filteredInfo } = this;
            sortedInfo = sortedInfo || {};
            filteredInfo = filteredInfo || {};
            const columns = [
                {
                title: "保单号",
                dataIndex: "policy_no",
                key: "policy_no",
                },
                {
                title: "车牌",
                dataIndex: "plate_no",
                key: "plate_no",
                },
                {
                title: "投保电话",
                dataIndex: "mobile",
                key: "mobile",
                },
                {
                title: "有效通次",
                dataIndex: "num_valid",
                key: "num_valid",
                sorter: (a, b) => a.num_valid - b.num_valid,
                sortOrder: sortedInfo.columnKey === "num_valid" && sortedInfo.order
                },
                {
                title: "通话时长",
                dataIndex: "talk_time",
                key: "talk_time",
                sorter: (a, b) => a.talk_time - b.talk_time,
                sortOrder: sortedInfo.columnKey === "talk_time" && sortedInfo.order
                },
                {
                title: "算价次数",
                dataIndex: "num_calculate",
                key: "num_calculate",
                sorter: (a, b) => a.num_calculate - b.num_calculate,
                sortOrder: sortedInfo.columnKey === "num_calculate" && sortedInfo.order
                },
                {
                title: "团队",
                dataIndex: "team",
                key: "team",
                sorter: (a, b) => a.team - b.team,
                sortOrder: sortedInfo.columnKey === "team" && sortedInfo.order
                },
            ];
            return columns;
        }
    },
    methods:{
        handleRemove(file) {
            const index = this.fileList.indexOf(file);
            const newFileList = this.fileList.slice();
            newFileList.splice(index, 1);
            this.fileList = newFileList
        },
        beforeUpload(file) {
            const isXLS = file.type === 'application/vnd.ms-excel' || file.type === 'text/csv' || file.type ==='application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
            if (!isXLS) {
                this.$notification['warning']({
                    message: '你只能上传xls,xlsx或csv格式文件',
                })
            }else{
                this.fileList = [file]
                this.dataSource = [] //成功选择新的文件时，结果列表清空
            }   
            return false;
        },
        handleUpload() {
            const { fileList } = this;
            const formData = new FormData();
            fileList.forEach((file) => {
                formData.append('files[]', file);
            });
            this.uploading = true

            // fetch('/api/v1/upload',{
            //     method:'post',
            //     body:data
            //     })
            //     .then(response => response.json())
            //     .then(resp => {
            //     if(resp.code === 200){
            //         console.log(resp);
            //         avatarImg.setAttribute('src',resp.data.url);
            //     }
            // })

            getPerformanceResult(formData).then((res) => {
                this.uploading = false
                this.dataSource = res.result
                this.$notification['success']({
                    message: '上传并分析文件成功！',
                })
            }).catch((err) => {
                this.uploading = false
                this.$notification['error']({
                    message: '上传并分析文件失败！',
                })
            });


            // fetch('https://www.mocky.io/v2/5cc8019d300000980a055e76',{
            //     method:'post',
            //     body:formData
            //     })
            //     .then(response => response.json())
            //     .then(resp => {
            //         getPerformanceResult().then((res) => {
            //             // this.fileList = []
            //             this.uploading = false
            //             this.dataSource = res.result
            //             this.$notification['success']({
            //                 message: '上传并分析文件成功！',
            //             })
            //         })
            //     }).catch((err)=>{
            //         this.uploading = false
            //         this.$notification['error']({
            //             message: '上传并分析文件失败！',
            //         })
            //     })

            // You can use any AJAX library you like
            // reqwest({
            //     url: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
            //     method: 'post',
            //     processData: false,
            //     data: formData,
            //     success: () => {
            //         debugger
            //         this.fileList = []
            //         this.uploading = false
            //         // this.dataSource = res.result
            //         this.$message.success('upload successfully.');
            //     },
            //     error: () => {
            //         debugger
            //         
            //     },
            // });
        },
        handleChange(pagination, filters, sorter) {
            this.filteredInfo = filters;
            this.sortedInfo = sorter;
        },
    }

}
</script>

<style>

</style>



