import { axios } from '@/utils/request'

const api = {
    performanceResult: '/performanceresult',
}
export default api

/**
 * login func
 * parameter: {
 *     username: '',
 *     password: '',
 *     remember_me: true,
 * }
 * @param parameter
 * @returns {*}
 */
export function getPerformanceResult(parameter) {
    return axios({
        url: api.performanceResult,
        method: 'post',
        data: parameter
    })
}


