<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="export"
            @click="showExportModal"
          >
            导出
          </a-button>
        </a-button-group>
        <a-button
          type="primary"
          icon="search"
          @click="showQueryModel"
        >
          <span>查询</span>
          <queryModal ref="dialogtreeQueryModal"></queryModal>
        </a-button>
      </div>
    </template>
    <div class="callStatistics">
      <!-- <span style="margin-left: 8px">
      <template v-if="hasSelected">
        {{`Selected ${selectedRowKeys.length} items`}}
      </template>
    </span> -->
      <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :dataSource="dataSource"
        :columns="columns"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :pagination="pagination"
        :loading="loading"
      >
        <template
          slot="operation"
          slot-scope="text, record"
        >
          <a
            href="javascript:;"
            @click="onDelete(record.key)"
          >踢除</a>
        </template>
      </a-table>
    </div>
  </page-layout>
</template>

<script>
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import { setTimeout, setImmediate } from "timers";
import queryModal from "./QueryModal";
import PageLayout from "@/components/Page/PageLayout";
const AButtonGroup = AButton.Group
export default {
  props: {
    customerId: String
  },
  components: {
    ATable,
    AButton,
    AButtonGroup,
    queryModal,
    PageLayout
  },
  data() {
    return {
      dataSource: [],
      selectedRowKeys: [],
      loading: false,
      loadingCallHistory: false,
      filteredInfo: null,
      sortedInfo: null,
      pagination: {
        showQuickJumper: true,
        pageSize: 10
      }
    };
  },
  mounted() {
    this.getDataFromServer();
  },
  watch: {
    customerId: {
      handler: function(customerId) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.deleteLocalHistory();
        }, 1000);
      }
    }
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let { sortedInfo, filteredInfo } = this;
      sortedInfo = sortedInfo || {};
      filteredInfo = filteredInfo || {};
      const columns = [
        {
          title: "呼叫时间",
          dataIndex: "CallDuration",
          key: "CallDuration",
          sorter: (a, b) => a.CallDuration.length - b.CallDuration.length,
          sortOrder: sortedInfo.columnKey === "CallDuration" && sortedInfo.order
        },
        {
          title: "时间",
          dataIndex: "duration",
          key: "duration",
          sorter: (a, b) => a.duration.length - b.duration.length,
          sortOrder: sortedInfo.columnKey === "duration" && sortedInfo.order
        },
        {
          title: "总通次",
          dataIndex: "totalCommunicateTime",
          key: "totalCommunicateTime",
          sorter: (a, b) =>
            a.totalCommunicateTime.length - b.totalCommunicateTime.length,
          sortOrder:
            sortedInfo.columnKey === "totalCommunicateTime" && sortedInfo.order
        },
        {
          title: "有效通次",
          dataIndex: "validCommunicateTime",
          key: "validCommunicateTime",
          sorter: (a, b) =>
            a.validCommunicateTime.length - b.validCommunicateTime.length,
          sortOrder:
            sortedInfo.columnKey === "validCommunicateTime" && sortedInfo.order
        },
        {
          title: "通时",
          dataIndex: "communicateDuration",
          key: "communicateDuration",
          sorter: (a, b) =>
            a.communicateDuration.length - b.communicateDuration.length,
          sortOrder:
            sortedInfo.columnKey === "communicateDuration" && sortedInfo.order
        }
      ];
      return columns;
    }
  },
  methods: {
    //      批量删除
    // batchDelete() {
    //   let _this = this;
    //   this.$confirm({
    //     title: "你确定要删除选中的客户信息吗?",
    //     content: "Some descriptions",
    //     okText: "确定",
    //     okType: "danger",
    //     cancelText: "取消",
    //     onOk() {
    //       _this.loading = true;
    //       // ajax request after empty completing
    //       setTimeout(() => {
    //         _this.loading = false;
    //         _this.dataSource = _this.dataSource.filter(
    //           item => !_this.selectedRowKeys.includes(item.key)
    //         );
    //         _this.selectedRowKeys = [];
    //       }, 1000);
    //     },
    //     onCancel() {
    //     }
    //   });
    // },
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    showExportModal() {},
    getDataFromServer() {
      //TODO:通过AJAX请求获得呼叫历史信息（如果仅显示该坐席的与该客户的呼叫记录，那么参数参数为坐席id+客户id）
      for (let i = 0; i < 25; i++) {
        this.dataSource.push({
          key: i,
          id: i,
          CallDuration: i,
          duration: i,
          totalCommunicateTime: i,
          validCommunicateTime: i,
          communicateDuration: i
        });
      }
    },
    deleteLocalHistory() {
      this.dataSource = [];
    },
    handleChange(pagination, filters, sorter) {
      this.filteredInfo = filters;
      this.sortedInfo = sorter;
    },
    onDelete(key) {
      let _this = this;
      this.$confirm({
        title: "你确定要踢除该坐席吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            const dataSource = [..._this.dataSource];
            _this.dataSource = dataSource.filter(item => item.key !== key);
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showQueryModel() {
      this.$refs.dialogtreeQueryModal.showQueryModal();
    }
  }
};
</script>

<style>
</style>



